import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {connect} from "react-redux";
import OraModal from "eshop/modules/core/components/ui/Modal";

export default class CartErrorModal extends Component  {
    constructor (props) {
        super(props);
        this.onReady = this.onReady.bind(this);
        this.state = {ready: false}
    }

    componentDidMount(){
        OPL.UI.on('module.started', (data) => this.onReady(data));
    }

    onReady(data){
        if(data.element.id === "react-modal-CartErrorModal-trigger"){
            console.log("CART_ERROR_MODAL: "+data.element.id)
            this.setState({ready: true});
        }
    }

    render(){
        let errorCode = this.props.errorCode;
        let errorTitle = null;
        let errorDescription = null;
        let errorIcon = null;
        if(errorCode) {
            errorTitle = this.props.descriptions["cartError." + errorCode + ".title"];
            errorDescription = this.props.descriptions["cartError." + errorCode + ".description"];
            errorIcon = this.props.descriptions["cartError." + errorCode + ".icon"];
        }

        return(
            <OraModal id="CartErrorModal"
                      key="CartErrorModal"
                      open={!!errorCode && this.state.ready}
                      openOnStart="true"
                      size="narrow"
                      >
        <h2 className="h3 u-margin-l opl-modal-title-space">
        {errorTitle}
        </h2>
        <div>
            <p>{errorDescription}</p>
        </div>
            </OraModal>
        );
    }
}